.list{
    color: rgb(255, 255, 255);
    width: 350px;
    background-color: rgb(176, 86, 228);
    height: 50px;
    margin: 20px auto;
    border-radius:5px;
}

.list p{
    padding: 10px;
    position: relative;
}
.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}
.faicons{
    margin: 0 5px;
    font-size: 15px;
}

.list p input{
    background-color: transparent;
    border: 0;
    color: white;
    font-size: 18px;
    border-radius: 20px;
    padding: 5px;
}
.list p input:focus{
    outline: none;
}